.header {
  display: block;
  text-decoration: none;
  box-sizing: border-box;
  height: 44px;
  width: 100%;
  background-color: rgb(74, 144, 226);
  line-height: 44px;
  color: rgb(255, 255, 255);
  padding-left: 10px;
  cursor: pointer;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
}